import Navigator, { Types as NavTypes, NavigatorDelegateSelector as DelegateSelector } from 'reactxp-navigation';
import RX = require('reactxp');
import Types = require('./NavigatorViewTypes');

class NavigatorView extends RX.Component<Types.NavigatorViewProps, Types.NavigatorViewState> {

    public navigator: Navigator;
    public panelsId: Array<Types.PanelId>;
    private _didMount: boolean = false;

    constructor (props: Types.NavigatorViewProps) {
        super(props);
    }

    componentDidMount () {
        if (this.props.startRoute)
            this.resetRouteStack(this.props.startRoute);
        else
            this.resetRouteStack({routeId: 0, sceneConfigType: NavTypes.NavigatorSceneConfigType.Fade});
    }

    render () {
        return (
            <Navigator 
                ref={ this._onNavigatorRef } 
                renderScene={ this.renderScene } 
                transitionCompleted={ this._transitionCompleted } 
                transitionStarted={ this._transitionStarted } 
                cardStyle={ this.props.cardStyle } 
                delegateSelector={ DelegateSelector } 
            />
        );
    }

    private _onNavigatorRef = (navigator: Navigator) => {
        this.navigator = navigator;
        this.setState({navigatorReady: true});
    }

    private _transitionCompleted = () => {
        if (typeof this.props.transitionCompleted === 'function') this.props.transitionCompleted();
    }

    private _transitionStarted = () => {
        if (RX.Platform.getType() === 'web') {
            if ("activeElement" in document)
                (document.activeElement as HTMLElement).blur();
        }

        if (typeof this.props.transitionStarted === 'function') this.props.transitionStarted();
    }

    public renderScene = (navigatorRoute: NavTypes.NavigatorRoute) => {
        return (<RX.View />);
    }

    public resetRouteStack = (navigatorRoute: NavTypes.NavigatorRoute) => {
        if (navigatorRoute.routeId === 0)
            this.navigator.immediatelyResetRouteStack([{ routeId: 1, sceneConfigType: navigatorRoute.sceneConfigType }]);
        
        this.navigator.immediatelyResetRouteStack([ navigatorRoute ]);
    }
}

export = NavigatorView;



