import _ = require('lodash');
import RX = require('reactxp');
import Types = require('./ModalPanelTypes');
import Animated = require('./ModalPanelAnimated');

import WrapperStyles = require('./WrapperStyles');
import Styles = require('./ModalPanelStyles');

class ModalPanel extends RX.Component<Types.ModalPanelProps, Types.ModalPanelState> {
    state = {
        modalId: '',
        modalWidth: 0,
        modalHeight: 0,
        animatedStyle: {},
        staticStyle: {},
        maskAnimatedStyle: {},
        maskStaticStyle: {}
    };

    private _maskStyle: object = {};
    private _panelStyle: object = {};

    private _translationValue: Array<RX.Animated.Value>;
    private _wrapperTranslationValue: RX.Animated.Value;
    private _maskTranslationValue: RX.Animated.Value;

    constructor (props: Types.ModalPanelProps) {
        super(props);

        let _size = this._getModalSize(props.size);

        this._maskStyle = _.compact([Styles.Mask, props.maskStyle, this._getAlignStyle(props.position)]);
        this._panelStyle = _.compact([Styles.Panel, props.style]);

        this.state = {
            modalId: props.modalId,
            modalWidth: _size.width,
            modalHeight: _size.height,
            animatedStyle: {},
            staticStyle: {},
            maskAnimatedStyle: {},
            maskStaticStyle: {}
        };
    }

    componentDidMount () {
        this._getAnimated(true);

        if (RX.Platform.getType() === 'web') {
            if ("activeElement" in document)
                (document.activeElement as HTMLElement).blur();
        }

        let toValue = this.props.configType === Types.ModalPanelConfigType.Fade || 
            this.props.configType === Types.ModalPanelConfigType.Scale ? 
            1 : 0,
            animation = RX.Animated.timing(this._translationValue[0], {
                toValue: toValue,
                easing: RX.Animated.Easing.Default(),
                duration: this.props.duration
            }),
            animations = [animation],
            fullAnimations;

        if (this.props.maskFadeInOut ||
            this.props.configType === Types.ModalPanelConfigType.FadeWithSlideFromRight || 
            this.props.configType === Types.ModalPanelConfigType.FadeWithSlideFromLeft || 
            this.props.configType === Types.ModalPanelConfigType.FadeWithSlideFromTop || 
            this.props.configType === Types.ModalPanelConfigType.FadeWithSlideFromBottom
        ) {
            animations.push(RX.Animated.timing(this._maskTranslationValue, {
                toValue: 1,
                easing: RX.Animated.Easing.Default(),
                duration: this.props.duration
            }));
        }

        if (this.props.wrapperAnimation && 
            (this.props.configType === Types.ModalPanelConfigType.FloatFromRight || 
                this.props.configType === Types.ModalPanelConfigType.FloatFromLeft || 
                this.props.configType === Types.ModalPanelConfigType.FloatFromTop || 
                this.props.configType === Types.ModalPanelConfigType.FloatFromBottom
            )
        ) {
            animations.push(RX.Animated.timing(this._wrapperTranslationValue, {
                toValue: 0,
                easing: RX.Animated.Easing.Default(),
                duration: this.props.duration
            }));
        }

        fullAnimations = RX.Animated.parallel(animations);

        fullAnimations.start();
    }

    componentDidUpdate () {
        this._setActive();
    }

    render () {
        return (
            <RX.View 
                style={ Styles.default } 
                onLayout={ this._onLayout } 
                limitFocusWithin={ true } 
                ref={ 'content' }
            >
                <RX.Animated.View style={[this.state.maskStaticStyle, this.state.maskAnimatedStyle]}>
                    <RX.View 
                        style={[
                            this._maskStyle, 
                            { width: this.props.width, height: this.props.height }
                        ]}
                    >
                        {this.props.pressMaskToClose && <RX.View style={{ position: 'absolute', width: this.props.width, height: this.props.height }} 
                            onPress={ this.dismiss }
                        ></RX.View>}
                        <RX.Animated.View style={[this.state.staticStyle, this.state.animatedStyle]}>
                            <RX.View 
                                style={[
                                    this._panelStyle,
                                    { 
                                        width: this.state.modalWidth !== 0 && this.state.modalWidth, 
                                        height: this.state.modalHeight !== 0 && this.state.modalHeight, 
                                        maxHeight: this.props.size.maxHeight, 
                                        minHeight: this.props.size.minHeight, 
                                        maxWidth: this.props.size.maxWidth, 
                                        minWidth: this.props.size.minWidth
                                    }
                                ]}
                            >
                                { this.props.children }
                            </RX.View>
                        </RX.Animated.View>
                    </RX.View>
                </RX.Animated.View>
            </RX.View>
        );
    }

    public dismiss = (e?: any) => {
        this._getAnimated(false);

        let animation = RX.Animated.timing(this._translationValue[0], {
              toValue: 0,
              easing: RX.Animated.Easing.Default(),
              duration: this.props.duration
            }
        ),
        animations = [animation],
        fullAnimations;

        if (this.props.maskFadeInOut ||
            this.props.configType === Types.ModalPanelConfigType.FadeWithSlideFromRight || 
            this.props.configType === Types.ModalPanelConfigType.FadeWithSlideFromLeft || 
            this.props.configType === Types.ModalPanelConfigType.FadeWithSlideFromTop || 
            this.props.configType === Types.ModalPanelConfigType.FadeWithSlideFromBottom
        ) {
            animations.push(RX.Animated.timing(this._maskTranslationValue, {
                toValue: 0,
                easing: RX.Animated.Easing.Default(),
                duration: this.props.duration
            }));
        }

        if (this.props.wrapperAnimation && 
            (this.props.configType === Types.ModalPanelConfigType.FloatFromRight || 
                this.props.configType === Types.ModalPanelConfigType.FloatFromLeft || 
                this.props.configType === Types.ModalPanelConfigType.FloatFromTop || 
                this.props.configType === Types.ModalPanelConfigType.FloatFromBottom
            )
        ) {
            animations.push(RX.Animated.timing(this._wrapperTranslationValue, {
                toValue: 0,
                easing: RX.Animated.Easing.Default(),
                duration: this.props.duration
            }));
        }

        fullAnimations = RX.Animated.parallel(animations);

        fullAnimations.start(() => {
            this.props.wrapper.hideModal(this.state.modalId);
        });
    }

    private _setActive = () => {
        if (this.refs.content && RX.Platform.getType() === 'web') {
            (this.refs.content as RX.View).setFocusLimited(!this.props.active);
        }
    }

    private _getAnimated = (show: boolean) => {
        let _animated: any = {
            translationValue: RX.Animated.createValue(0),
            static: {},
            animated: {}
        }, 
        _maskAnimated = this.props.maskFadeInOut ||
            this.props.configType === Types.ModalPanelConfigType.FadeWithSlideFromRight || 
            this.props.configType === Types.ModalPanelConfigType.FadeWithSlideFromLeft || 
            this.props.configType === Types.ModalPanelConfigType.FadeWithSlideFromTop || 
            this.props.configType === Types.ModalPanelConfigType.FadeWithSlideFromBottom ? Animated.Fade(show) : {
                translationValue: RX.Animated.createValue(0),
                static: {},
                animated: {}
            },
        _wrapperAnimated: any = {
            translationValue: RX.Animated.createValue(0),
            static: WrapperStyles.defaultAnimated,
            animated: {}
        }, _offset;

        switch (this.props.configType) {
            case Types.ModalPanelConfigType.FloatFromLeft:
                _offset = this.props.position === Types.ModalPanelPosition.BottomLeft || 
                    this.props.position === Types.ModalPanelPosition.CenterLeft ||
                    this.props.position === Types.ModalPanelPosition.TopLeft ? 
                    this.state.modalWidth : 
                    this.props.width;
                _animated = Animated.FloatFromLeft(show, _offset);
                _wrapperAnimated = Animated.FloatFromRight(!show, _offset);
                _.extend(_wrapperAnimated.static, WrapperStyles.defaultWrapper);
                break;
            
            case Types.ModalPanelConfigType.FloatFromRight:
                _offset = this.props.position === Types.ModalPanelPosition.BottomRight || 
                    this.props.position === Types.ModalPanelPosition.CenterRight ||
                    this.props.position === Types.ModalPanelPosition.TopRight ? 
                    this.state.modalWidth : 
                    this.props.width;
                _animated = Animated.FloatFromRight(show, _offset);
                _wrapperAnimated = Animated.FloatFromLeft(!show, _offset);
                _.extend(_wrapperAnimated.static, WrapperStyles.defaultWrapper);
                break;

            case Types.ModalPanelConfigType.FloatFromTop:
                _offset = this.props.position === Types.ModalPanelPosition.TopLeft || 
                    this.props.position === Types.ModalPanelPosition.TopCenter ||
                    this.props.position === Types.ModalPanelPosition.TopRight ? 
                    this.state.modalHeight : 
                    this.props.height;
                _animated = Animated.FloatFromTop(show, _offset);
                _wrapperAnimated = Animated.FloatFromBottom(!show, _offset);
                _.extend(_wrapperAnimated.static, WrapperStyles.defaultWrapper);
                break;
            
            case Types.ModalPanelConfigType.FloatFromBottom:
                _offset = this.props.position === Types.ModalPanelPosition.BottomLeft || 
                    this.props.position === Types.ModalPanelPosition.BottomCenter ||
                    this.props.position === Types.ModalPanelPosition.BottomRight ? 
                    this.state.modalHeight : 
                    this.props.height;
                _animated = Animated.FloatFromBottom(show, _offset);
                _wrapperAnimated = Animated.FloatFromTop(!show, _offset);
                _.extend(_wrapperAnimated.static, WrapperStyles.defaultWrapper);
                break;
            
            case Types.ModalPanelConfigType.Fade:
                _animated = Animated.Fade(show);
                break;
            
            case Types.ModalPanelConfigType.FadeWithSlideFromRight:
                _animated = Animated.FadeWithSlideFromRight(show);
                break;
            
            case Types.ModalPanelConfigType.FadeWithSlideFromLeft:
                _animated = Animated.FadeWithSlideFromLeft(show);
                break;
            
            case Types.ModalPanelConfigType.FadeWithSlideFromTop:
                _animated = Animated.FadeWithSlideFromTop(show);
                break;
            
            case Types.ModalPanelConfigType.FadeWithSlideFromBottom:
                _animated = Animated.FadeWithSlideFromBottom(show);
                break;
            
            case Types.ModalPanelConfigType.Scale:
                _animated = Animated.Scale(show);
                break;

            default:
                break;
        }

        this._translationValue = Array.isArray(_animated.translationValue) ? _animated.translationValue : [_animated.translationValue];
        this._maskTranslationValue = _maskAnimated.translationValue;
        this._wrapperTranslationValue = _wrapperAnimated.translationValue;
        this.setState({
            animatedStyle: _animated.animated,
            staticStyle: _animated.static,
            maskAnimatedStyle: _maskAnimated.animated,
            maskStaticStyle: _maskAnimated.static
        });

        if (this.props.wrapperAnimation && 
            (this.props.configType === Types.ModalPanelConfigType.FloatFromRight || 
                this.props.configType === Types.ModalPanelConfigType.FloatFromLeft || 
                this.props.configType === Types.ModalPanelConfigType.FloatFromTop || 
                this.props.configType === Types.ModalPanelConfigType.FloatFromBottom
            )
        ) {
            this.props.wrapper.setState({
                animatedStyle: _wrapperAnimated.animated,
                staticStyle: _wrapperAnimated.static
            });
        }
    }

    private _onLayout = (e?: any) => {
        let _size = this._getModalSize(this.props.size),
            _wrapperStyle = this.props.wrapper.state.staticStyle;

        if (this.props.wrapperAnimation) {
            switch (this.props.configType) {
                case Types.ModalPanelConfigType.FloatFromBottom:
                    _wrapperStyle.top = this.props.position === Types.ModalPanelPosition.BottomLeft || 
                        this.props.position === Types.ModalPanelPosition.BottomCenter ||
                        this.props.position === Types.ModalPanelPosition.BottomRight ? 
                        -_size.height : 
                        -this.props.height;
                    break;

                case Types.ModalPanelConfigType.FadeWithSlideFromTop:
                    _wrapperStyle.top = this.props.position === Types.ModalPanelPosition.TopLeft || 
                        this.props.position === Types.ModalPanelPosition.TopRight ||
                        this.props.position === Types.ModalPanelPosition.TopCenter ? 
                        _size.height : 
                        this.props.height;
                    break;

                case Types.ModalPanelConfigType.FloatFromLeft:
                    _wrapperStyle.left = this.props.position === Types.ModalPanelPosition.BottomLeft || 
                        this.props.position === Types.ModalPanelPosition.TopLeft ||
                        this.props.position === Types.ModalPanelPosition.CenterLeft ? 
                        _size.width : 
                        this.props.width;
                    break;

                case Types.ModalPanelConfigType.FloatFromRight:
                    _wrapperStyle.left = this.props.position === Types.ModalPanelPosition.BottomRight || 
                        this.props.position === Types.ModalPanelPosition.TopRight ||
                        this.props.position === Types.ModalPanelPosition.CenterRight ? 
                        -_size.width : 
                        -this.props.width;
                    break;
                
                default:
                    // code...
                    break;
            }

            this.props.wrapper.setState({
                staticStyle: _wrapperStyle
            });
        }

        this.setState({
            modalWidth: _size.width,
            modalHeight: _size.height
        });
    }

    private _getModalSize = (size: Types.ModalPanelSize) => {
        let _size = {
            width: 0,
            height: 0
        },
        _height = this.props.height ? this.props.height : RX.UserInterface.measureWindow().height,
        _width = this.props.width ? this.props.width : RX.UserInterface.measureWindow().width;

        if (size.unit !== Types.ModalPanelSizeUnit.percent) {
            _size.width = size.width;
            _size.height = size.height !== 'auto' ? size.height : 0;
        } else {
            _size.width = _width / 100 * size.width;
            _size.height = size.height !== 'auto' ? _height / 100 * size.height : 0;
        }

        if (size.maxHeight)
            if (_size.height > size.maxHeight) _size.height = size.maxHeight;

        if (size.minHeight)
            if (_size.height < size.minHeight && _size.height !== 0) _size.height = size.minHeight;

        if (size.maxWidth)
            if (_size.width > size.maxWidth) _size.width = size.maxWidth;

        if (size.minWidth)
            if (_size.width < size.minWidth && _size.width !== 0) _size.width = size.minWidth;

        return _size;
    }

    private _getAlignStyle = (pos: Types.ModalPanelPosition) => {
        let _style = {
            justifyContent: 'center', 
            alignItems: 'center'
        };

        switch(pos) {
            case Types.ModalPanelPosition.CenterLeft:
                _style.alignItems = 'flex-start';
                break;
            case Types.ModalPanelPosition.CenterRight:
                _style.alignItems = 'flex-end';
                break;
            case Types.ModalPanelPosition.TopLeft:
                _style.justifyContent = 'flex-start';
                _style.alignItems = 'flex-start';
                break;
            case Types.ModalPanelPosition.TopRight:
                _style.justifyContent = 'flex-start';
                _style.alignItems = 'flex-end';
                break;
            case Types.ModalPanelPosition.TopCenter:
                _style.justifyContent = 'flex-start';
                break;
            case Types.ModalPanelPosition.BottomLeft:
                _style.justifyContent = 'flex-end';
                _style.alignItems = 'flex-start';
                break;
            case Types.ModalPanelPosition.BottomRight:
                _style.justifyContent = 'flex-end';
                _style.alignItems = 'flex-end';
                break;
            case Types.ModalPanelPosition.BottomCenter:
                _style.alignItems = 'flex-end';
                break;
        }

        return _style;
    }
}

export = ModalPanel;



