import { Types as NavTypes } from 'reactxp-navigation';
import RX = require('reactxp');
import {Panel as NavigatorPanel, Types} from './library/NavigatorRX';

import Route = require('./PopupRoute');

class PopupPanel extends NavigatorPanel {
    renderContent = () => {
        return (
            <RX.View>
                <RX.Text>POPUP</RX.Text>
            </RX.View>
        );
    }
}

export = PopupPanel;



