import RX = require('reactxp');
import { Types } from './library/NavigatorRX';

export enum NavigationRouteId {
    error,
    popup
}

export const PanelsId: Array<Types.PanelId> = [
    {
        htmlId: 'errorPopup',
        routeId: NavigationRouteId.error
    },
    {
        htmlId: 'popupPopup',
        routeId: NavigationRouteId.popup
    }
];



