import _ = require('lodash');
import React = require('react');
import ReactDOM = require('react-dom');
import RX = require('reactxp');
// import AccessibilityUtil from 'reactxp/src/web/AccessibilityUtil';
import Types = require('reactxp/src/common/Types');

import Styles = require('./TextRXStyles');

export class P extends RX.Component<Types.TextProps, {}> {
    private _style: object  = {};

    constructor (props: Types.TextProps) {
        super(props);

        this._style = _.compact([Styles.defaultText, this.props.style]);
    }

    render () {
        if (RX.Platform.getType() === 'web')
            return this.renderHTML()
        else
            return this._renderApp();
    }

    _getStyles(): Types.TextStyleRuleSet {
        // There's no way in HTML to properly handle numberOfLines > 1,
        // but we can correctly handle the common case where numberOfLines is 1.
        let combinedStyles = RX.Styles.combine([this.props.numberOfLines === 1 ?
            Styles.ellipsis : Styles.defaultTag, this.props.style]) as any;

        // Handle cursor styles
        if (this.props.selectable) {
            combinedStyles['cursor'] = 'text';
            combinedStyles['userSelect'] = 'text';
            combinedStyles['WebkitUserSelect'] = 'text';
            combinedStyles['MozUserSelect'] = 'text';
            combinedStyles['msUserSelect'] = 'text';
        } else {
            combinedStyles['cursor'] = 'inherit';
        }

        if (this.props.onPress) {
            combinedStyles['cursor'] = 'pointer';
        }

        return combinedStyles;
    }

    public renderHTML = () => {
        const isAriaHidden = false;//AccessibilityUtil.isHidden(this.props.importantForAccessibility);

        if (this.props.selectable || typeof this.props.children !== 'string') {
            return (
                <p
                    style={ this._getStyles() as any }
                    aria-hidden={ isAriaHidden }
                    onClick={ this.props.onPress }
                    id={ this.props.id }
                    onContextMenu={ this.props.onContextMenu }
                >
                    { this.props.children }
                </p>
            );
        } else {
            return (
                <p
                    style={ this._getStyles() as any }
                    aria-hidden={ isAriaHidden }
                    onClick={ this.props.onPress }
                    onContextMenu={ this.props.onContextMenu }
                    data-text-as-pseudo-element={ this.props.children }
                    id={ this.props.id }
                />
            );
        }
    }

    private _renderApp = () => {
        return (
            <RX.Text 
                style={ this._style } 
                onPress={ this.props.onPress } 
                selectable={ this.props.selectable } 
                allowFontScaling={ this.props.allowFontScaling } 
                ellipsizeMode={ this.props.ellipsizeMode } 
                id={ this.props.id } 
                importantForAccessibility={ this.props.importantForAccessibility } 
                maxContentSizeMultiplier={this.props.maxContentSizeMultiplier} 
                onContextMenu={ this.props.onContextMenu }
            >
                { this.props.children }
            </RX.Text>
        )
    }
}



