/*
* This file demonstrates a basic ReactXP app.
*/
// This example uses ExperimentalNavigation on iOS and Android
import { Types as NavTypes } from 'reactxp-navigation';
import RX = require('reactxp');
import Wrapper = require('./library/Wrapper');
import ButtonRX = require('./library/ButtonRX');

import MainNavigator = require('./MainNavigator');
import Route = require('./Route');

const styles = {
    // Standard navigator style should be an object. So we have to disable caching here.
    navCardStyle: RX.Styles.createViewStyle({
        backgroundColor: '#f5fcff'
    }, false)
};

class App extends RX.Component<{}> {
    render() {
        return (
            <Wrapper.View style={{minWidth: 320}}>
                <RX.View style={{height: 30}}>
                    <RX.Button onPress={this._mainNavBack}>
                        <RX.Text>Back</RX.Text>
                    </RX.Button>
                    <RX.Button onPress={this._modalOpen}>
                        <RX.Text>Open Modal</RX.Text>
                    </RX.Button>
                </RX.View>
                <Wrapper.View ref={'mainWrapper'}>
                    <MainNavigator transitionCompleted={ this._transitionCompleted } ref={'mainNav'} startRoute={{ routeId: Route.NavigationRouteId.MainPanel, sceneConfigType: NavTypes.NavigatorSceneConfigType.Fade }} />
                </Wrapper.View>
            </Wrapper.View>
        );
    }

    componentDidMount () {
        (this.refs.mainWrapper as Wrapper.View).addModal([{
            modalId: 'testModal',
            size: {
                width: 70,
                height: 100,
                unit: Wrapper.ModalSizeUnit.percent,
                minWidth: 240,
                maxWidth: 320
            },
            ele: (
                <RX.View style={{flex: 1}}>
                    <ButtonRX.Basic onPress={this._mainNavPage1} style={{marginTop: 16}}>Main Panel</ButtonRX.Basic>
                    <ButtonRX.Basic onPress={this._mainNavPage2} style={{marginTop: 16}}>Second Panel</ButtonRX.Basic>
                    <ButtonRX.Basic onPress={this._mainNavPage3} style={{marginTop: 16}}>Test Panel</ButtonRX.Basic>
                    <ButtonRX.Basic onPress={this._mainNavPage4} style={{marginTop: 16}}>Test Panel 2</ButtonRX.Basic>
                    <ButtonRX.Basic onPress={this._popup} style={{marginTop: 16}}>Popup</ButtonRX.Basic>
                </RX.View>
            ),
            show: false,
            maskStyle: {
                backgroundColor: 'rgba(0, 0, 0, .4)'
            },
            position: Wrapper.ModalPosition.TopLeft,
            pressMaskToClose: true,
            configType: Wrapper.ModalConfigType.FloatFromLeft,
            wrapperAnimation: true
        },{
            modalId: 'testModal2',
            size: {
                width: 70,
                height: 70,
                unit: Wrapper.ModalSizeUnit.percent
            },
            ele: (
                <RX.View style={{flex: 1}}>
                    <RX.Text>TEST POPUP</RX.Text>
                    <ButtonRX.Basic onPress={this._mainNavBack} style={{marginTop: 16}}>Popup</ButtonRX.Basic>
                </RX.View>
            ),
            show: false,
            maskStyle: {
                backgroundColor: 'rgba(0, 0, 0, .4)'
            },
            position: Wrapper.ModalPosition.Center,
            pressMaskToClose: true,
            configType: Wrapper.ModalConfigType.FadeWithSlideFromBottom
        }]);
    }

    private _mainNavBack = () => {
        (this.refs.mainWrapper as Wrapper.View).hideAllModalWithAnimation();
        (this.refs.mainNav as MainNavigator).navigator.pop();
    }

    private _mainNavPage1 = () => {
        (this.refs.mainWrapper as Wrapper.View).hideAllModalWithAnimation();
        (this.refs.mainNav as MainNavigator).navigator.replace({
            routeId: Route.NavigationRouteId.MainPanel,
            sceneConfigType: NavTypes.NavigatorSceneConfigType.FloatFromRight
        });
    }

    private _mainNavPage2 = () => {
        (this.refs.mainWrapper as Wrapper.View).hideAllModalWithAnimation();
        (this.refs.mainNav as MainNavigator).navigator.replace({
            routeId: Route.NavigationRouteId.SecondPanel,
            sceneConfigType: NavTypes.NavigatorSceneConfigType.FloatFromRight
        });
    }

    private _mainNavPage3 = () => {
        (this.refs.mainWrapper as Wrapper.View).hideAllModalWithAnimation();
        (this.refs.mainNav as MainNavigator).navigator.replace({
            routeId: Route.NavigationRouteId.TestPanel,
            sceneConfigType: NavTypes.NavigatorSceneConfigType.FloatFromRight
        });
    }

    private _mainNavPage4 = () => {
        (this.refs.mainWrapper as Wrapper.View).hideAllModalWithAnimation();
        (this.refs.mainNav as MainNavigator).navigator.replace({
            routeId: Route.NavigationRouteId.TestPanel2,
            sceneConfigType: NavTypes.NavigatorSceneConfigType.FloatFromRight
        });
    }

    private _popup = () => {
        (this.refs.mainWrapper as Wrapper.View).showModal('testModal2');
    }

    private _transitionCompleted = () => {
        // console.log('transitionCompleted');
        // let _currentRoutes = (this.refs.mainNav as any).navigator.getCurrentRoutes();
        // console.log(_currentRoutes);
        // (this.refs.mainNav as any).resetRouteStack(_currentRoutes[_currentRoutes.length - 1]);
    }

    private _modalOpen = () => {
        (this.refs.mainWrapper as Wrapper.View).showModal('testModal');
    }
}

export = App;



