import _ = require('lodash');
import RX = require('reactxp');
import ReactDOM = require('react-dom');

import Types = require('./ButtonRXTypes');
import * as TextRX from '../TextRX';

export class Basic extends RX.Component<Types.ButtonProps, Types.ButtonState> {
    state = {
        hovering: false,
        readOnly: false
    };

    public buttonStyle: object;
    public textStyle: object;

    private _buttonStyle: object;
    private _textStyle: object;
    private _hoverButtonStyle: object;
    private _hoverTextStyle: object;

    constructor(props: Types.ButtonProps) {
        super(props);

        this.defaultStyle();

        this._buttonStyle = _.compact([this.buttonStyle, this.props.style]);
        this._textStyle = _.compact([this.textStyle, this.props.textStyle]);
        this._hoverButtonStyle = _.compact([this._buttonStyle, this.props.hoverStyle]);
        this._hoverTextStyle = _.compact([this._textStyle, this.props.hoverTextStyle]);

        this.state = {
            hovering: false,
            readOnly: false
        };
    }

    handleButtonHover = (e: any) => {
        if (!this.props.disabled) this.setState({ hovering: e.type === 'mouseenter' });
        if (typeof this.props.onHoverStart === 'function' && e.type === 'mouseenter')
            this.props.onHoverStart(e);
        if (typeof this.props.onHoverEnd === 'function' && e.type !== 'mouseenter')
            this.props.onHoverEnd(e);
    }

    render () {
        return (
            <RX.Button 
                style={ [ this._buttonStyle, this.state.hovering && this._hoverButtonStyle ] } 
                accessibilityLabel={ this.props.accessibilityLabel } 
                accessibilityTraits={ this.props.accessibilityTraits } 
                ariaControls={ this.props.ariaControls } 
                id={ this.props.id } 
                importantForAccessibility={ this.props.importantForAccessibility } 
                delayLongPress={ this.props.delayLongPress } 
                disabled={ this.props.disabled } 
                onAccessibilityTapIOS={ !this.state.readOnly && this.props.onAccessibilityTapIOS } 
                onLongPress={ !this.state.readOnly && this.props.onLongPress } 
                onHoverStart={ !this.state.readOnly && this.handleButtonHover } 
                onHoverEnd={ !this.state.readOnly && this.handleButtonHover } 
                onPress={ !this.state.readOnly && this.props.onPress } 
                onPressIn={ !this.state.readOnly && this.props.onPressIn } 
                onPressOut={ !this.state.readOnly && this.props.onPressOut } 
                shouldRasterizeIOS={ this.props.shouldRasterizeIOS } 
                tabIndex={ this.state.readOnly ? -1 : this.props.tabIndex } 
                title={ this.props.title } 
                disableTouchOpacityAnimation={ this.props.disableTouchOpacityAnimation } 
                activeOpacity={ this.props.activeOpacity } 
                underlayColor={ this.props.underlayColor }
            >
                { this._renderText() }
            </RX.Button>
        );
    }

    public defaultStyle () {
        this.buttonStyle = {
            borderRadius: typeof this.props.radius === 'number' ? this.props.radius : 4,
            backgroundColor: '#7d88a9',
            paddingTop: 1,
            paddingBottom: 1,
            paddingLeft: 1,
            paddingRight: 1,
            justifyContent: this.props.justifyContent ? this.props.justifyContent : 'center', 
            alignItems: this.props.justifyContent ? this.props.justifyContent : 'center',
            transition: 'background-color 0.2s'
        };

        if (this.props.icons || this.props.iconOnly === true) {
            delete (this.buttonStyle as any).justifyContent;
            delete (this.buttonStyle as any).alignItems;
        }

       this.textStyle = {
            color: '#ffffff',
            transition: 'color 0.2s'
        };

        if (RX.Platform.getType() !== 'web') {
            delete (this.buttonStyle as any).transition;
            delete (this.textStyle as any).transition;
        }
    }

    public focus = () => {
        if (RX.Platform.getType() === 'web') {
            (ReactDOM.findDOMNode(this) as HTMLInputElement).focus();
        }
    }

    public blur = () => {
        if (RX.Platform.getType() === 'web') {
            (ReactDOM.findDOMNode(this) as HTMLInputElement).blur();
        }
    }

    public setReadonly = (onlyRead: boolean) => {
        this.setState({ readOnly: onlyRead });
    }

    private _renderText () {
        let _padding: number = typeof this.props.padding === 'number' ? this.props.padding : 16,
            _iconSize: number = typeof this.props.iconSize === 'number' ? this.props.iconSize : 16,
            _textStyle: object = {
                marginTop: _padding / 2,
                marginBottom: _padding / 2,
                marginLeft: _padding,
                marginRight: _padding
            },
            _justify: any = typeof this.props.justifyContent === 'string' ? this.props.justifyContent : 'center',
            _direction:any = 'row',
            _align: any = 'center';

        if (this.props.icons || this.props.iconOnly === true) {
            let _startIcon: Types.ButtonIcon = null, 
                _endIcon: Types.ButtonIcon = null,
                _iconStyle = {
                    width: _iconSize,
                    height: _iconSize
                },
                _btnViewStyle = {
                    flexDirection: _direction,
                    justifyContent: _justify, 
                    alignItems: _align,
                    paddingLeft: _padding,
                    paddingRight: _padding
                },
                _btnIconOnlyStyle = {
                    paddingTop: _padding,
                    paddingBottom: _padding
                };

            if (Array.isArray(this.props.icons)) {
                for (let i:number = 0; i < this.props.icons.length; i++) {
                    if (this.props.icons[i].pos === 'start') _startIcon = this.props.icons[i];
                    else _endIcon = this.props.icons[i];
                }
            } else {
                if (this.props.icons.pos === 'start') _startIcon = this.props.icons;
                else _endIcon = this.props.icons;
            }

            if (_startIcon !== null)
                (_textStyle as any).marginLeft = _padding / 2;
            else 
                (_textStyle as any).marginLeft = 0;

            if (_endIcon !== null)
                (_textStyle as any).marginRight = _padding / 2;
            else
                (_textStyle as any).marginRight = 0;

            return (
                <RX.View style={[ _btnViewStyle, this.props.iconOnly === true && _btnIconOnlyStyle, this.props.disabled && this.props.disabledStyle ]}>
                    { 
                        _startIcon !== null && 
                        <RX.Image 
                            source={ this.props.disabled && _startIcon.disabledSource ? (_startIcon.disabledSource) : (this.state.hovering && _startIcon.hoverSource ? (_startIcon.hoverSource) : (_startIcon.source)) } 
                            style={ [_iconStyle, _startIcon.style, this.state.hovering && _startIcon.hoverStyle, this.props.disabled && _startIcon.disabledStyle] }
                            title={ _startIcon.title }
                            accessibilityLabel={ _startIcon.accessibilityLabel }
                        /> 
                    }
                    { this.props.iconOnly !== true && 
                        <TextRX.SPAN style={ [_textStyle, this._textStyle, this.state.hovering && this._hoverTextStyle ] }>
                            { this.props.children }
                        </TextRX.SPAN>
                    }
                    { 
                        _endIcon !== null && 
                        <RX.Image 
                            source={ this.props.disabled && _endIcon.disabledSource ? (_endIcon.disabledSource) : (this.state.hovering && _endIcon.hoverSource ? (_endIcon.hoverSource) : (_endIcon.source)) } 
                            style={ [_iconStyle, _endIcon.style, this.state.hovering && _endIcon.hoverStyle, this.props.disabled && _endIcon.disabledStyle] }
                            title={ _endIcon.title }
                            accessibilityLabel={ _endIcon.accessibilityLabel }
                        /> 
                    }
                </RX.View>
            );
        } else {
            return (
                <TextRX.SPAN style={ [_textStyle, this._textStyle, this.state.hovering && this._hoverTextStyle, this.props.disabled && this.props.disabledTextStyle ] }>
                    { this.props.children }
                </TextRX.SPAN>
            );
        }
    }
}



