import _ = require('lodash');
import { Types as NavTypes } from 'reactxp-navigation';
import RX = require('reactxp');
import {View as NavigatorView, Types} from './library/NavigatorRX';

import PopupPanel = require('./PopupPanel');
import Route = require('./PopupRoute');

const styles = {
    scroll: RX.Styles.createScrollViewStyle({
        alignSelf: 'stretch',
        backgroundColor: '#000000'
    })
};

class MainNavigator extends NavigatorView {
    constructor (props: Types.NavigatorViewProps) {
        super(props);

        this.panelsId = Route.PanelsId;
    }

    componentDidMount() {
        this.resetRouteStack({
            routeId: Route.NavigationRouteId.popup,
            sceneConfigType: NavTypes.NavigatorSceneConfigType.Fade
        });
    }

    renderScene = (navigatorRoute: NavTypes.NavigatorRoute) => {
        let _panelId = _.filter(this.panelsId, item => item.routeId === navigatorRoute.routeId),
            _htmlId = _panelId.length > 0 ? _panelId[0] : { routeId: 0, htmlId: ''};
        
        switch (navigatorRoute.routeId) {
            case Route.NavigationRouteId.error:
                return (<RX.Text>ERROR</RX.Text>);
            case Route.NavigationRouteId.popup:
                return <PopupPanel navigator={ this.navigator } panelId={ _htmlId } style={ styles.scroll } />;
        }

        return (<RX.View />);
    }
}

export = MainNavigator;



