import RX = require('reactxp');
import WrapperView = require('./WrapperView');
import WrapperViewTypes = require('./WrapperViewTypes');
import ModalPanel = require('./ModalPanel');
import ModalPanelTypes = require('./ModalPanelTypes');

module Wrapper {
    export class View extends WrapperView {};
    export class Modal extends ModalPanel {};
    export var ModalConfigType: typeof ModalPanelTypes.ModalPanelConfigType = ModalPanelTypes.ModalPanelConfigType;
    export var ModalSizeUnit: typeof ModalPanelTypes.ModalPanelSizeUnit = ModalPanelTypes.ModalPanelSizeUnit;
    export var ModalPosition: typeof ModalPanelTypes.ModalPanelPosition = ModalPanelTypes.ModalPanelPosition;
    export namespace Types {
        export interface ModalElement extends WrapperViewTypes.ModalElement {};
        export interface ViewProps extends WrapperViewTypes.WrapperViewProps {};
        export interface ModalSize extends ModalPanelTypes.ModalPanelSize {};
        export interface ModalProps extends ModalPanelTypes.ModalPanelProps {};
    }
};

export = Wrapper;



