import RX = require('reactxp');
import Types = require('./CheckboxTypes');
import {ButtonIcon as IconTypes} from './../ButtonRX/ButtonRXTypes';
import ButtonRX = require('./../ButtonRX');

const icon = require('./../../../images/checkbox-blank-outline.png'),
        checkedIcon = require('./../../../images/checkbox-marked-outline.png');

class Checkbox extends RX.Component<Types.CheckboxProps, Types.CheckboxState> {
    state = {
        checked: false
    };

    public icons:IconTypes = {
        source: icon,
        pos: 'start'
    };

    public checkedIcons:IconTypes = {
        source: checkedIcon,
        pos: 'start'
    };

    constructor(props: Types.CheckboxProps) {
        super(props);

        this.state = {
            checked: props.checked
        }
    }

    render () {
        return (
            <ButtonRX.Clear 
                style={[ this.props.style, this.state.checked && this.props.checkedStyle ]} 
                accessibilityLabel={ this.props.accessibilityLabel } 
                accessibilityTraits={ this.props.accessibilityTraits } 
                ariaControls={ this.props.ariaControls } 
                id={ this.props.id } 
                importantForAccessibility={ this.props.importantForAccessibility } 
                delayLongPress={ this.props.delayLongPress } 
                disabled={ this.props.disabled } 
                onAccessibilityTapIOS={ this.props.onAccessibilityTapIOS } 
                onLongPress={ this.props.onLongPress } 
                onHoverStart={ this.props.onHoverStart } 
                onHoverEnd={ this.props.onHoverEnd } 
                onPress={ this.handleChange } 
                onPressIn={ this.props.onPressIn } 
                onPressOut={ this.props.onPressOut } 
                shouldRasterizeIOS={ this.props.shouldRasterizeIOS } 
                tabIndex={ this.props.tabIndex } 
                title={ this.props.title } 
                disableTouchOpacityAnimation={ this.props.disableTouchOpacityAnimation } 
                activeOpacity={ this.props.activeOpacity } 
                underlayColor={ this.props.underlayColor }
                textStyle={[ this.props.textStyle, this.state.checked && this.props.checkedTextStyle ]} 
                hoverTextStyle={ this.props.hoverTextStyle } 
                hoverStyle={ this.props.hoverStyle } 
                disabledStyle={ this.props.disabledStyle } 
                disabledTextStyle={ this.props.disabledTextStyle } 
                icons={ this.state.checked ? (this.props.checkedIcons ? this.props.checkedIcons : this.checkedIcons) : (this.props.icons ? this.props.icons : this.icons) } 
                radius={ this.props.radius } 
                iconOnly={ this.props.iconOnly } 
                iconSize={ this.props.iconSize } 
                padding={ this.props.padding } 
                justifyContent={ this.props.justifyContent }
            >
                { this.props.children }
            </ButtonRX.Clear>
        );
    }

    public handleChange = (e: any) => {
        let _check = !this.state.checked;
        this.setState({ checked: _check });

        if (typeof this.props.onChange === 'function') this.props.onChange(e, _check, this.props.value);
        if (typeof this.props.onPress === 'function') this.props.onPress(e);
    }
}

export = Checkbox;



