/*
* This file demonstrates a basic ReactXP app.
*/
import { Types as NavTypes } from 'reactxp-navigation';
import React = require('react');
import ReactDOM = require('react-dom');
import RX = require('reactxp');

import {Panel as NavigatorPanel, Types} from './library/NavigatorRX';
import Badges = require('./library/Badges');
// import Banner = require('./library/Banner');
import Card = require('./library/Card');
import Checkbox = require('./library/Checkbox/Checkbox');
import CheckboxGroup = require('./library/Checkbox/CheckboxGroup');
import CheckboxGroupTypes = require('./library/Checkbox/CheckboxGroupTypes');
import Radio = require('./library/Radio/Radio');
import RadioGroup = require('./library/Radio/RadioGroup');
import RadioGroupTypes = require('./library/Radio/RadioGroupTypes');
import * as TextRX from './library/TextRX';
import * as ButtonRX from './library/ButtonRX';
import * as ButtonRXTypes from './library/ButtonRX/ButtonRXTypes';

import Route = require('./Route');

const pickerItems: RX.Types.PickerPropsItem[] = [
    {
        label: 'Cool',
        value: 'cool'
    },
    {
        label: 'Super',
        value: 'super'
    },
    {
        label: 'Great',
        value: 'great'
    }
];

const checkboxData: Array<CheckboxGroupTypes.CheckboxData> = [
    {
        label: 'Cool',
        ref: 'checkboxCool',
        value: 'cool'
    },
    {
        label: 'Super',
        ref: 'checkboxSuper',
        value: 'super'
    },
    {
        label: 'Great',
        ref: 'checkboxGreat',
        value: 'great'
    }
]; 

const radioData: Array<RadioGroupTypes.RadioData> = [
    {
        label: 'Cool',
        ref: 'cool',
        value: 'cool'
    },
    {
        label: 'Super',
        ref: 'super',
        value: 'super'
    },
    {
        label: 'Great',
        ref: 'great',
        value: 'great'
    }
]; 

const styles = {
    container: RX.Styles.createViewStyle({
        padding: 16,
        justifyContent: 'center',
        alignItems: 'center'
    }),
    helloWorldAnimat: RX.Styles.createTextStyle({
        marginBottom: 28
    }),
    helloWorld: RX.Styles.createTextStyle({
        fontSize: 48,
        fontWeight: 'bold'
    }),
    welcome: RX.Styles.createTextStyle({
        fontSize: 32,
        marginBottom: 12
    }),
    instructions: RX.Styles.createTextStyle({
        fontSize: 16,
        color: '#aaa',
        marginBottom: 16,
        flex: 1,
        textAlignVertical: 'top'
    }),
    docLink: RX.Styles.createLinkStyle({
        fontSize: 16,
        color: 'blue',
        marginBottom: 16
    }),
    roundButton: RX.Styles.createViewStyle({
        margin: 16,
        borderRadius: 16,
        backgroundColor: '#7d88a9'
    }),
    buttonText: RX.Styles.createTextStyle({
        fontSize: 16,
        marginVertical: 6,
        marginHorizontal: 12,
        color: 'white'
    })
};

class MainPanel extends NavigatorPanel {
    state = {
        width: 0,
        checkedItems: [''],
        radioValue: '',
        selectedValue: 'cool',
        test: true,
        active: false
    };

    private _translationValue: RX.Animated.Value;
    private _animatedStyle: RX.Types.AnimatedTextStyleRuleSet;

    constructor(props: Types.NavigatorPanelProps) {
        super(props);

        this._translationValue = RX.Animated.createValue(-100);
        this._animatedStyle = RX.Styles.createAnimatedTextStyle({
            transform: [
                {
                    translateY: this._translationValue
                }
            ]
        });

        this.state = {
            width: RX.UserInterface.measureWindow().width,
            checkedItems: [],
            radioValue: '',
            selectedValue: 'cool',
            test: true,
            active: false
        };
    }

    componentDidMount() {
        let animation = RX.Animated.timing(this._translationValue, {
              toValue: 0,
              easing: RX.Animated.Easing.OutBack(),
              duration: 500
            }
        );

        animation.start();

        (this.refs.focusTest as ButtonRX.Type).setReadonly(true);
    }

    renderContent = () => {
        let _icon = require('./../images/icon.png'),
            _icon2 = require('./../images/icon2.png');

        return (
            <RX.View style={{flex: 1}}>
                <RX.View style={ styles.container }>
                    <RX.Animated.Text style={ [styles.helloWorldAnimat, this._animatedStyle] }>
                        <TextRX.H1 ref={'header1'} style={ styles.helloWorld }>Hello World</TextRX.H1>
                    </RX.Animated.Text>
                    <TextRX.H2 style={ styles.welcome }>
                        Welcome to <TextRX.SPAN style={{ color: '#ff0000' }} onPress={ this._onPressNavigate }>ReactXP</TextRX.SPAN>
                    </TextRX.H2>
                    <RX.View style={{flexDirection: 'row'}}>
                        <TextRX.P style={ styles.instructions } selectable={true}>
                            Edit App.tsx to get started
                        </TextRX.P>
                        <Badges>{ RX.Platform.getType() }</Badges>
                    </RX.View>
                    <RX.Link style={ styles.docLink } url={ 'https://microsoft.github.io/reactxp/docs' }>
                        View ReactXP documentation
                    </RX.Link>
                    <TextRX.P>Width: {this.state.width}</TextRX.P>
                    <RX.Button style={ styles.roundButton } onPress={ this._onPressNavigate }>
                        <TextRX.SPAN style={ styles.buttonText }>
                            See More Examples
                        </TextRX.SPAN>
                    </RX.Button>
                    <ButtonRX.Basic 
                        disabled={ false } 
                        hoverStyle={{backgroundColor: '#ff0000'}} 
                        onPress={ this._onFocus } 
                        iconOnly={ true } 
                        radius={ 999 } 
                        padding={ 8 }
                        icons={[{ pos: 'start', source: _icon, hoverSource: _icon2 }]}
                    />
                    <ButtonRX.Basic 
                        ref={'focusTest'} 
                        textStyle={ styles.buttonText } 
                        hoverTextStyle={{color: '#000000'}} 
                        hoverStyle={{backgroundColor: '#ff0000'}} 
                        onPress={ this._onPressNavigate } 
                        icons={[{ pos: 'start', source: _icon, hoverSource: _icon2 }, { pos: 'end', source: _icon, hoverSource: _icon2 }]}
                    >
                        See More Examples
                    </ButtonRX.Basic>
                    <ButtonRX.Outline 
                        textStyle={ [ styles.buttonText, { color: '#7d88a9' } ] } 
                        hoverTextStyle={{color: '#ff0000'}} 
                        hoverStyle={{borderColor: '#ff0000'}} 
                        onPress={ this._onPressNavigate }
                    >
                        See More Examples
                    </ButtonRX.Outline>
                    <ButtonRX.Clear 
                        textStyle={ [ styles.buttonText, { color: '#7d88a9' } ] } 
                        hoverTextStyle={{color: '#ff0000'}} 
                        onPress={ this._onPressNavigate }
                    >
                        See More Examples
                    </ButtonRX.Clear>

                    <CheckboxGroup onChange={this._onGroupChange} data={checkboxData} ref={'checkboxGroup1'}></CheckboxGroup>

                    <TextRX.P>{this.state.checkedItems}</TextRX.P>

                    <RadioGroup data={ radioData } ref={'radioGroup1'} onChange={this._onGroupChange}></RadioGroup>

                    <TextRX.P>{this.state.radioValue}</TextRX.P>

                    
                </RX.View>
                <Card style={{padding: 16}}>
                    <TextRX.P style={{ fontSize: 16 }}>
                        This is just your basic card with some text to boot. Like it? Keep scrolling...
                    </TextRX.P>
                </Card>
                <Card style={{padding: 16}}>
                    <TextRX.H3 style={{ fontWeight: 'bold', fontSize: 16, paddingBottom: 16 }}>
                        Header
                    </TextRX.H3>
                    <TextRX.P style={{fontSize: 16}}>
                        The British use the term "header", but the American term "head-shot" the English simply refuse to adopt.
                    </TextRX.P>
                </Card>
                <Card>
                    <TextRX.H3 style={{ fontWeight: 'bold', fontSize: 16, color: '#488aff', padding: 16 }}>
                        Header
                    </TextRX.H3>
                    <RX.View style={{ borderColor: '#c8c7cc', borderTopWidth: 1, borderBottomWidth: 0, borderLeftWidth: 0, borderRightWidth: 0, borderStyle: 'solid' }}>
                        <ButtonRX.Clear 
                            radius={ 0 } 
                            textStyle={{ color: '#000000', flex:1, marginTop: 11, marginBottom: 11, fontSize: 16 }} 
                            hoverTextStyle={{color: '#000000'}} 
                            onPress={ this._onPressNavigate } 
                            iconSize={ 30 } 
                            justifyContent={ 'flex-start' } 
                            icons={[
                                { 
                                    pos: 'start', 
                                    source: _icon
                                }, 
                                { 
                                    pos: 'end', 
                                    source: _icon2
                                }
                            ]}
                        >
                           Button 1
                        </ButtonRX.Clear>
                        <ButtonRX.Clear 
                            radius={ 0 } 
                            textStyle={{ color: '#000000', flex:1, marginTop: 11, marginBottom: 11, fontSize: 16 }} 
                            hoverTextStyle={{color: '#000000'}} 
                            onPress={ this._onPressNavigate } 
                            iconSize={ 30 } 
                            justifyContent={ 'flex-start' } 
                            icons={[
                                { 
                                    pos: 'start', 
                                    source: _icon
                                }, 
                                { 
                                    pos: 'end', 
                                    source: _icon2
                                }
                            ]}
                        >
                           Button 2
                        </ButtonRX.Clear>
                        <ButtonRX.Clear 
                            radius={ 0 } 
                            textStyle={{ color: '#000000', flex:1, marginTop: 11, marginBottom: 11, fontSize: 16 }} 
                            hoverTextStyle={{color: '#000000'}} 
                            onPress={ this._onPressNavigate } 
                            iconSize={ 30 } 
                            justifyContent={ 'flex-start' } 
                            icons={[
                                { 
                                    pos: 'start', 
                                    source: _icon
                                }, 
                                { 
                                    pos: 'end', 
                                    source: _icon2
                                }
                            ]}
                        >
                           Button 3
                        </ButtonRX.Clear>
                        <ButtonRX.Clear 
                            radius={ 0 } 
                            textStyle={{ color: '#000000', flex:1, marginTop: 11, marginBottom: 11, fontSize: 16 }} 
                            hoverTextStyle={{color: '#000000'}} 
                            onPress={ this._onPressNavigate } 
                            iconSize={ 30 } 
                            justifyContent={ 'flex-start' } 
                            icons={[
                                { 
                                    pos: 'start', 
                                    source: _icon
                                }, 
                                { 
                                    pos: 'end', 
                                    source: _icon2
                                }
                            ]}
                        >
                           Button 4
                        </ButtonRX.Clear>
                    </RX.View>
                </Card>
                <Card>
                    <RX.View style={{ padding: 16 }}>
                        <TextRX.H3 style={{ fontWeight: 'bold', fontSize: 16, paddingBottom: 16 }}>
                            Header
                        </TextRX.H3>
                        <TextRX.P style={{fontSize: 16}}>
                            The British use the term "header", but the American term "head-shot" the English simply refuse to adopt.
                        </TextRX.P>
                    </RX.View>
                </Card>
            </RX.View>
        );


                    // <Banner source={ 'https://ionicframework.com/dist/preview-app/www/assets/img/nin-live.png' } radius={[ 2, 2, 0, 0 ]}>
                    //     <TextRX.H2 style={{ color: '#ffffff', alignSelf: 'center' }}>
                    //         Banner
                    //     </TextRX.H2>
                    // </Banner>
    }

    private _onFocus = () => {
        (ReactDOM.findDOMNode(this.refs.header1 as any) as HTMLInputElement).focus();
    }
    
    private _onPressNavigate = () => {
        if (this.props.navigator) {
            this.props.navigator.push({
                routeId: Route.NavigationRouteId.TestPanel,
                sceneConfigType: NavTypes.NavigatorSceneConfigType.FloatFromRight
            });
        }
    }

    private _onValueChange = (itemValue: string, itemIndex: number) => {
        this.setState({ selectedValue: itemValue });
    }

    private _onClear = (e: any) => {
        console.log(this.refs);
        if ((this.refs.myCheckbox as any).state.checked) (this.refs.myCheckbox as any).handleChange({event: 'clear'});
    }

    private _onGroupChange = (e: any, val: any) => {
        console.log('Group Value Change');
        console.log(val);

        if (Array.isArray(val)) this.setState({checkedItems: val});
        else this.setState({radioValue: val});
    }
}

export = MainPanel;



