import RX = require('reactxp');
import {ButtonIcon as IconTypes} from './../ButtonRX/ButtonRXTypes';
import Checkbox = require('./../Checkbox/Checkbox');

const icon = require('./../../../images/radiobox-blank.png'),
        checkedIcon = require('./../../../images/radiobox-marked.png');

class Radio extends Checkbox {
    icons: IconTypes = {
        source: icon,
        pos: 'start'
    };

    checkedIcons: IconTypes = {
        source: checkedIcon,
        pos: 'start'
    };

    handleChange = (e: any) => {
        if (!this.state.checked) {
            let _check = !this.state.checked;
            this.setState({ checked: _check });

            if (typeof this.props.onChange === 'function') this.props.onChange(e, _check, this.props.value);
            if (typeof this.props.onPress === 'function') this.props.onPress(e);
        }
    }

    private _clearChecked = (e: any) => {
        this.setState({ checked: false });

        if (typeof this.props.onChange === 'function') this.props.onChange(e, false, this.props.value);
        if (typeof this.props.onPress === 'function') this.props.onPress(e);
    }
}

export = Radio;



