import _ = require('lodash');
import { Types as NavTypes } from 'reactxp-navigation';
import RX = require('reactxp');
import {View as NavigatorView, Types} from './library/NavigatorRX';

import MainPanel = require('./MainPanel');
import SecondPanel = require('./SecondPanel');
import TestPanel = require('./TestPanel');
import TestPanel2 = require('./TestPanel2');
import Route = require('./Route');

const styles = {
    scroll: RX.Styles.createScrollViewStyle({
        alignSelf: 'stretch',
        backgroundColor: '#f5fcff'
    })
};

class MainNavigator extends NavigatorView {
    constructor (props: Types.NavigatorViewProps) {
        super(props);

        this.panelsId = Route.PanelsId;
    }

    renderScene = (navigatorRoute: NavTypes.NavigatorRoute) => {
        let _panelId = _.filter(this.panelsId, item => item.routeId === navigatorRoute.routeId),
            _htmlId = _panelId.length > 0 ? _panelId[0] : { routeId: 0, htmlId: ''};
        
        switch (navigatorRoute.routeId) {
            case Route.NavigationRouteId.MainPanel:
                return <MainPanel navigator={ this.navigator } panelId={ _htmlId } style={ styles.scroll } />;
            case Route.NavigationRouteId.SecondPanel:
                return <SecondPanel navigator={ this.navigator } panelId={ _htmlId } style={ styles.scroll } />;
            case Route.NavigationRouteId.TestPanel:
                return <TestPanel navigator={ this.navigator } panelId={ _htmlId } />;
            case Route.NavigationRouteId.TestPanel2:
                return <TestPanel2 navigator={ this.navigator } panelId={ _htmlId } />;
        }

        return (<RX.View />);
    }
}

export = MainNavigator;



