import React = require('react');
import ReactDOM = require('react-dom');
import RX = require('reactxp');
import { P as P } from './P';

export class H2 extends P {
    renderHTML = function () {
        const isAriaHidden = false;//AccessibilityUtil.isHidden(this.props.importantForAccessibility);

        if (this.props.selectable || typeof this.props.children !== 'string') {
            return (
                <h2
                    style={ this._getStyles() as any }
                    aria-hidden={ isAriaHidden }
                    onClick={ this.props.onPress }
                    id={ this.props.id }
                    onContextMenu={ this.props.onContextMenu }
                >
                    { this.props.children }
                </h2>
            );
        } else {
            return (
                <h2
                    style={ this._getStyles() as any }
                    aria-hidden={ isAriaHidden }
                    onClick={ this.props.onPress }
                    onContextMenu={ this.props.onContextMenu }
                    data-text-as-pseudo-element={ this.props.children }
                    id={ this.props.id }
                />
            );
        }
    }
}



