import { Types as NavTypes } from 'reactxp-navigation';
import RX = require('reactxp');
import {Panel as NavigatorPanel, Types} from './library/NavigatorRX';
import ButtonRX = require('./library/ButtonRX');
import PopupNavigator = require('./PopupNavigator');

import Route = require('./Route');

class TestPanel2 extends NavigatorPanel {
    renderContent = () => {
        return (
            <RX.View>
                <ButtonRX.Basic onPress={this.back}>Back</ButtonRX.Basic>
                <ButtonRX.Basic onPress={this._onPressNavigate} style={{marginTop: 16}}>Second Panel</ButtonRX.Basic>
                <ButtonRX.Basic onPress={this._onPressNavigate2} style={{marginTop: 16}}>Main Panel</ButtonRX.Basic>
                <ButtonRX.Basic onPress={this._onPressNavigate3} style={{marginTop: 16}}>Test Panel</ButtonRX.Basic>
                <ButtonRX.Basic ref={'popupBtn'} style={{marginTop: 16}} onPress={this._onPopup}>Popup</ButtonRX.Basic>
            </RX.View>
        );
    }

    componentDidUpdate() {
        let animation = RX.Animated.timing(RX.Animated.createValue(-100), {
              toValue: 0,
              easing: RX.Animated.Easing.OutBack(),
              duration: 500
            }
        );

        animation.start();

        this.setActive();
    }

    private _onPressNavigate = () => {
        this.goto({
            routeId: Route.NavigationRouteId.SecondPanel,
            sceneConfigType: NavTypes.NavigatorSceneConfigType.FloatFromRight
        });
    }

    private _onPressNavigate2 = () => {
        this.goto({
            routeId: Route.NavigationRouteId.MainPanel,
            sceneConfigType: NavTypes.NavigatorSceneConfigType.FloatFromRight
        });
    }

    private _onPressNavigate3 = () => {
        this.goto({
            routeId: Route.NavigationRouteId.TestPanel,
            sceneConfigType: NavTypes.NavigatorSceneConfigType.FloatFromRight
        });
    }

    private _translationValue: RX.Animated.Value;
    private _animatedStyle: RX.Types.AnimatedTextStyleRuleSet;

    private _onPopup = () => {
        this._translationValue = RX.Animated.createValue(-100);
        this._animatedStyle = RX.Styles.createAnimatedTextStyle({
            transform: [
                {
                    translateY: this._translationValue
                }
            ]
        });

        RX.Modal.show((<RX.View><PopupNavigator /></RX.View>), 'testPopup');
    }

    private _onClosePopop = () => {
        RX.Modal.dismiss('testPopup');
    }
}

export = TestPanel2;



