import _ = require('lodash');
import RX = require('reactxp');
import Types = require('./ModalPanelRXTypes');

const _styles = {
    default: RX.Styles.createViewStyle({
        flex: 1
    }),
    Mask: RX.Styles.createViewStyle({
        flex: 1
    }),
    Panel: RX.Styles.createViewStyle({
        backgroundColor: '#ffffff'
    })
};

class ModalPanelRX extends RX.Component<Types.ModalPanelProps, Types.ModalPanelState> {
    state = {
        modalId: '',
        width: RX.UserInterface.measureWindow().width,
        height: RX.UserInterface.measureWindow().height,
        modalWidth: 0,
        modalHeight: 0
    };

    private _maskStyle: object = {};
    private _panelStyle: object = {};

    constructor (props: Types.ModalPanelProps) {
        super(props);

        let _size = this._getModalSize(props.size);

        this._maskStyle = _.compact([_styles.Mask, props.maskStyle, this._getAlignStyle(props.position)]);
        this._panelStyle = _.compact([_styles.Panel, props.style]);

        this.state = {
            modalId: props.modalId,
            width: RX.UserInterface.measureWindow().width,
            height: RX.UserInterface.measureWindow().height,
            modalWidth: _size.width,
            modalHeight: _size.height
        };
    }

    render () {
        return (
            <RX.View 
                style={ _styles.default } 
                onLayout={ this._onLayout }
            >
                <RX.View 
                    style={[
                        this._maskStyle, 
                        { width: this.state.width, height: this.state.height }
                    ]}
                >
                    {this.props.pressMaskToClose && <RX.View style={{ position: 'absolute', width: this.state.width, height: this.state.height }} 
                        onPress={ this.close }
                    ></RX.View>}
                    <RX.View 
                        style={[
                            this._panelStyle,
                            { width: this.state.modalWidth !== 0 && this.state.modalWidth, height: this.state.modalHeight !== 0 && this.state.modalHeight }
                        ]}
                    >
                        { this.props.children }
                    </RX.View>
                </RX.View>
            </RX.View>
        );
    }

    public close = (e?: any) => {
        RX.Modal.dismiss(this.state.modalId);
    }

    private _onLayout = (e?: any) => {
        let _size = this._getModalSize(this.props.size);

        this.setState({
            width: RX.UserInterface.measureWindow().width,
            height: RX.UserInterface.measureWindow().height,
            modalWidth: _size.width,
            modalHeight: _size.height
        });
    }

    private _getModalSize = (size: Types.ModalPanelSize) => {
        let _size = {
            width: 0,
            height: 0
        };

        if (size.unit !== Types.ModalPanelSizeUnit.percent) {
            _size.width = size.width;
            _size.height = size.height !== 'auto' ? size.height : 0;
        } else {
            _size.width = RX.UserInterface.measureWindow().width / 100 * size.width;
            _size.height = size.height !== 'auto' ? RX.UserInterface.measureWindow().height / 100 * size.height : 0;
        }

        return _size;
    }

    private _getAlignStyle = (pos: Types.ModalPanelPosition) => {
        let _style = {
            justifyContent: 'center', 
            alignItems: 'center'
        };

        switch(pos) {
            case Types.ModalPanelPosition.CenterLeft:
                _style.alignItems = 'flex-start';
                break;
            case Types.ModalPanelPosition.CenterRight:
                _style.alignItems = 'flex-end';
                break;
            case Types.ModalPanelPosition.TopLeft:
                _style.justifyContent = 'flex-start';
                _style.alignItems = 'flex-start';
                break;
            case Types.ModalPanelPosition.TopRight:
                _style.justifyContent = 'flex-start';
                _style.alignItems = 'flex-end';
                break;
            case Types.ModalPanelPosition.TopCenter:
                _style.justifyContent = 'flex-start';
                break;
            case Types.ModalPanelPosition.BottomLeft:
                _style.justifyContent = 'flex-end';
                _style.alignItems = 'flex-start';
                break;
            case Types.ModalPanelPosition.BottomRight:
                _style.justifyContent = 'flex-end';
                _style.alignItems = 'flex-end';
                break;
            case Types.ModalPanelPosition.BottomCenter:
                _style.alignItems = 'flex-end';
                break;
        }

        return _style;
    }
}

export = ModalPanelRX;



