import RX = require('reactxp');
import NavPanel = require('./NavigatorPanel');
import NavPanelTypes = require('./NavigatorPanelTypes');
import NavView = require('./NavigatorView');
import NavViewTypes = require('./NavigatorViewTypes');

module NavigatorRX {
    export class Panel extends NavPanel {};
    export class View extends NavView {};
    export namespace Types {
        export interface NavigatorPanelProps extends NavPanelTypes.NavigatorPanelProps {};
        export interface NavigatorViewProps extends NavViewTypes.NavigatorViewProps {};
        export interface PanelId extends NavViewTypes.PanelId {};
    }
};

export = NavigatorRX;



