import RX = require('reactxp');
import {H1 as H1Imp} from './H1';
import {H2 as H2Imp} from './H2';
import {H3 as H3Imp} from './H3';
import {H4 as H4Imp} from './H4';
import {H5 as H5Imp} from './H5';
import {H6 as H6Imp} from './H6';
import {P as PImp} from './P';
import {SPAN as SPANImp} from './SPAN';

module TextRX {
    export class H1 extends H1Imp {};
    export class H2 extends H2Imp {};
    export class H3 extends H3Imp {};
    export class H4 extends H4Imp {};
    export class H5 extends H5Imp {};
    export class H6 extends H6Imp {};
    export class P extends PImp {};
    export class SPAN extends SPANImp {};
};

export = TextRX;



