import RX = require('reactxp');
import Types = require('./library/NavigatorRX/NavigatorViewTypes');

export enum NavigationRouteId {
    MainPanel,
    SecondPanel,
    TestPanel,
    TestPanel2
}

export const PanelsId: Array<Types.PanelId> = [
    {
        htmlId: 'mainPanel',
        routeId: NavigationRouteId.MainPanel
    },
    {
        htmlId: 'secondPanel',
        routeId: NavigationRouteId.SecondPanel
    },
    {
        htmlId: 'testPanel',
        routeId: NavigationRouteId.TestPanel
    },
    {
        htmlId: 'testPanel2',
        routeId: NavigationRouteId.TestPanel2
    },
];



