import RX = require('reactxp');
import Types = require('reactxp/src/common/Types');

export enum ModalPanelConfigType {
    FloatFromRight,
    FloatFromLeft,
    FloatFromTop,
    FloatFromBottom,
    Fade,
    FadeWithSlide
}

export enum ModalPanelSizeUnit {
    px,
    percent
}

export enum ModalPanelPosition {
    Center,
    CenterLeft,
    CenterRight,
    TopLeft,
    TopRight,
    TopCenter,
    BottomLeft,
    BottomRight,
    BottomCenter
}

export interface ModalPanelSize {
    width: number;
    height: number | 'auto';
    unit?: ModalPanelSizeUnit;
}

export interface ModalPanelProps extends Types.ViewProps {
    modalId: string;
    size: ModalPanelSize;
    position?: ModalPanelPosition;
    maskStyle?: Types.ViewStyle;
    configType?: ModalPanelConfigType;
    pressMaskToClose?: boolean;
}

export interface ModalPanelState {
    modalId: string;
    width: number;
    height: number;
    modalWidth: number;
    modalHeight: number;
}



