import RX = require('reactxp');

export function FloatFromRight (show: boolean, offsetX: number) {
    let _offsetX = show ? offsetX : -offsetX,
        styles = {
            translationValue: RX.Animated.createValue(_offsetX),
            static: {},
            animated: {}
        };

    if (show) {
        styles.animated = RX.Styles.createAnimatedViewStyle({
            transform: [
                {
                    translateX: styles.translationValue
                }
            ]
        });
    } else {
        styles.animated = RX.Styles.createAnimatedViewStyle({
            transform: [
                {
                    translateX: styles.translationValue
                }
            ]
        });

        styles.static = { 
            left: -_offsetX
        };
    }

    return styles;
};

