import _ = require('lodash');
import RX = require('reactxp');
import Types = require('reactxp/src/common/Types');

const _styles = {
    defaultBadges: RX.Styles.createTextStyle({
        alignSelf: 'flex-start',
        fontSize: 14,
        fontWeight: 'bold',
        backgroundColor: '#488aff',
        color: '#ffffff',
        paddingLeft: 8,
        paddingRight: 8,
        paddingTop: 4,
        paddingBottom: 4,
        borderRadius: 999,
        marginLeft: 8,
        marginRight: 0
    })
};

class Badges extends RX.Component<Types.TextProps> {
    private _style: object  = {};

    constructor (props: Types.TextProps) {
        super(props);

        this._style = _.compact([_styles.defaultBadges, this.props.style]);
    }

    render () {
        return (
            <RX.Text
                style={ this._style }
                onPress={ this.props.onPress } 
                selectable={ this.props.selectable }
                allowFontScaling={ this.props.allowFontScaling } 
                ellipsizeMode={ this.props.ellipsizeMode } 
                id={ this.props.id } 
                importantForAccessibility={ this.props.importantForAccessibility } 
                maxContentSizeMultiplier={this.props.maxContentSizeMultiplier} 
                onContextMenu={ this.props.onContextMenu }
            >
                { this.props.children }
            </RX.Text>
        );
    }
}

export = Badges;



