import RX = require('reactxp');

const styles = {
    defaultText: RX.Styles.createTextStyle({
        color: '#000000'
    }),
    defaultTag: {
        marginTop: 0,
        marginBottom: 0,
        fontWeight: 'normal',
        position: 'relative',
        display: 'inline',
        flexGrow: 0,
        flexShrink: 0,
        overflow: 'hidden',
        whiteSpace: 'pre-wrap',
        overflowWrap: 'break-word',
        msHyphens: 'auto'
    },
    ellipsis: {
        position: 'relative',
        display: 'inline',
        flexGrow: 0,
        flexShrink: 0,
        overflow: 'hidden',
        whiteSpace: 'pre',
        textOverflow: 'ellipsis'
    }
};

export = styles;


