import _ = require('lodash');
import React = require('react');
import ReactDOM = require('react-dom');
import Navigator, { Types as NavTypes, NavigatorDelegateSelector as DelegateSelector } from 'reactxp-navigation';
import RX = require('reactxp');
import Types = require('./NavigatorPanelTypes');

import Styles = require('./NavigatorPanelStyles');

class NavigatorPanel extends RX.Component<Types.NavigatorPanelProps, Types.NavigatorPanelState> {
    private _style: object  = {};

    constructor (props: Types.NavigatorPanelProps) {
        super(props);

        this._style = _.compact([Styles.defaultPanel, this.props.style]);
    }

    componentDidUpdate () {
        this.setActive();
    }

    render () {
        return (
            <RX.ScrollView  
                style={ this._style } 
                bounces={ this.props.bounces } 
                horizontal={ this.props.horizontal } 
                vertical={ this.props.vertical } 
                justifyEnd={ this.props.justifyEnd } 
                keyboardDismissMode={ this.props.keyboardDismissMode } 
                keyboardShouldPersistTaps={ this.props.keyboardShouldPersistTaps } 
                onContentSizeChange={ this.props.onContentSizeChange } 
                onScroll={ this.props.onScroll } 
                onScrollBeginDrag={ this.props.onScrollBeginDrag } 
                onScrollEndDrag={ this.props.onScrollEndDrag } 
                overScrollMode={ this.props.overScrollMode } 
                pagingEnabled={ this.props.pagingEnabled } 
                snapToInterval={ this.props.snapToInterval } 
                scrollEnabled={ this.props.scrollEnabled } 
                scrollEventThrottle={ this.props.scrollEventThrottle } 
                scrollIndicatorInsets={ this.props.scrollIndicatorInsets } 
                scrollsToTop={ this.props.scrollsToTop } 
                showsHorizontalScrollIndicator={ this.props.showsHorizontalScrollIndicator } 
                showsVerticalScrollIndicator={ this.props.showsVerticalScrollIndicator } 
                onLayout={ this.props.onLayout }  
            >
                <RX.View ref={ 'content' } limitFocusWithin={ true } style={{ flex: 1 }} id={ this.props.panelId.htmlId + 'View' }>
                    { this._renderView() }
                </RX.View>
            </RX.ScrollView>
        );
    }

    public setActive = () => {
        if (RX.Platform.getType() === 'web') {
            let _navigator = (this.props.navigator as Navigator),
                _currentRoutes = _navigator.getCurrentRoutes();

            if (this.refs.content) 
                (this.refs.content as RX.View).setFocusLimited(_currentRoutes[_navigator.state.presentedIndex].routeId !== this.props.panelId.routeId);
        }
    }

    public renderContent = () => {
        return (
            <RX.View>
                { this.props.children }
            </RX.View>
        );
    }

    public back = (e?: any) => {
        this.props.navigator.pop();
    }

    public goto = (navigatorRoute: NavTypes.NavigatorRoute) => {
        let _currRoutes = this.props.navigator.getCurrentRoutes(),
            _filterRoutes = _.findLastIndex(_currRoutes, {routeId: navigatorRoute.routeId});

        if (_filterRoutes === -1)
            this.props.navigator.push(navigatorRoute);
    }

    private _renderView = () => {
        if (RX.Platform.getType() === 'web') {
            return (                <div
                    style={{ }} 
                    id={ this.props.panelId.htmlId }
                >
                    { this.renderContent() }
                </div>
            );
        } else {
            return (
                <RX.View
                    style={{ flex: 1 }} 
                    id={ this.props.panelId.htmlId }
                >
                    { this.renderContent() }
                </RX.View>
            );
        }
    }
}

export = NavigatorPanel;



