import RX = require('reactxp');
import Types = require('reactxp/src/common/Types');
import WrapperView = require('./WrapperView');

export enum ModalPanelConfigType {
    FloatFromRight,
    FloatFromLeft,
    FloatFromTop,
    FloatFromBottom,
    Fade,
    FadeWithSlideFromRight,
    FadeWithSlideFromLeft,
    FadeWithSlideFromTop,
    FadeWithSlideFromBottom,
    Scale
}

export enum ModalPanelSizeUnit {
    px,
    percent
}

export enum ModalPanelPosition {
    Center,
    CenterLeft,
    CenterRight,
    TopLeft,
    TopRight,
    TopCenter,
    BottomLeft,
    BottomRight,
    BottomCenter
}

export interface ModalPanelSize {
    width: number;
    height: number | 'auto';
    unit?: ModalPanelSizeUnit;
    minWidth?: number;
    maxWidth?: number;
    minHeight?: number;
    maxHeight?: number;
}

export interface ModalPanelProps extends Types.ViewProps {
    width: number;
    height: number;
    wrapper: WrapperView;
    modalId: string;
    size: ModalPanelSize;
    position?: ModalPanelPosition;
    maskStyle?: Types.ViewStyle;
    maskFadeInOut?: boolean;
    configType?: ModalPanelConfigType;
    pressMaskToClose?: boolean;
    wrapperAnimation?: boolean;
    active?: boolean;
    duration?: number;
}

export interface ModalPanelState {
    modalId: string;
    modalWidth: number;
    modalHeight: number;
    animatedStyle: RX.Types.AnimatedViewStyleRuleSet;
    staticStyle: RX.Types.ViewStyle;
    maskAnimatedStyle: RX.Types.AnimatedViewStyleRuleSet;
    maskStaticStyle: RX.Types.ViewStyle;
}



