import RX = require('reactxp');
import Radio = require('./Radio');
import Types = require('./RadioGroupTypes');

class RadioGroup extends RX.Component<Types.RadioGroupProps, Types.RadioGroupState> {
    constructor(props: Types.RadioGroupProps) {
        super(props);

        this.state = {
            value: ''
        };
    }

    render () {
        let _radio = [],
            _data: Array<any> = Array.isArray(this.props.data) ? this.props.data : [this.props.data];

        for (let i = 0; i < _data.length; i++) {
            _radio.push(this.renderRadio(_data[i], i));
        }

        return (
            <RX.View 
                style={ this.props.style } 
                accessibilityLabel={ this.props.accessibilityLabel } 
                accessibilityTraits={ this.props.accessibilityTraits } 
                importantForAccessibility={ this.props.importantForAccessibility } 
                animateChildEnter={ this.props.animateChildEnter } 
                animateChildLeave={ this.props.animateChildLeave } 
                animateChildMove={ this.props.animateChildMove } 
                ariaLabelledBy={ this.props.ariaLabelledBy } 
                blockPointerEvents={ this.props.blockPointerEvents } 
                id={ this.props.id } 
                ignorePointerEvents={ this.props.ignorePointerEvents } 
                restrictFocusWithin={ this.props.restrictFocusWithin } 
                limitFocusWithin={ this.props.limitFocusWithin } 
                importantForLayout={ this.props.importantForLayout } 
                onDragEnter={ this.props.onDragEnter } 
                onDragOver={ this.props.onDragOver } 
                onDragLeave={ this.props.onDragLeave } 
                onDrop={ this.props.onDrop } 
                onMouseEnter={ this.props.onMouseEnter } 
                onMouseLeave={ this.props.onMouseLeave } 
                onMouseMove={ this.props.onMouseMove } 
                onMouseOver={ this.props.onMouseOver } 
                onContextMenu={ this.props.onContextMenu } 
                onPress={ this.props.onPress } 
                onLongPress={ this.props.onLongPress } 
                onMoveShouldSetResponder={ this.props.onMoveShouldSetResponder } 
                onMoveShouldSetResponderCapture={ this.props.onMoveShouldSetResponderCapture } 
                onResponderGrant={ this.props.onResponderGrant } 
                onResponderReject={ this.props.onResponderReject } 
                onResponderRelease={ this.props.onResponderRelease } 
                onResponderStart={ this.props.onResponderStart } 
                onResponderMove={ this.props.onResponderMove } 
                onResponderEnd={ this.props.onResponderEnd } 
                onResponderTerminate={ this.props.onResponderTerminate } 
                onResponderTerminationRequest={ this.props.onResponderTerminationRequest } 
                onStartShouldSetResponder={ this.props.onStartShouldSetResponder } 
                onStartShouldSetResponderCapture={ this.props.onStartShouldSetResponderCapture } 
                onLayout={ this.props.onLayout } 
                shouldRasterizeIOS={ this.props.shouldRasterizeIOS } 
                tabIndex={ this.props.tabIndex } 
                title={ this.props.title } 
                viewLayerTypeAndroid={ this.props.viewLayerTypeAndroid } 
                disableTouchOpacityAnimation={ this.props.disableTouchOpacityAnimation } 
                activeOpacity={ this.props.activeOpacity } 
                underlayColor={ this.props.underlayColor }
            >
                { _radio }
            </RX.View>
        );
    }

    public renderRadio = (data: Types.RadioData, key: number) => {
        return (
            <Radio 
                key={key} 
                value={ data.value } 
                ref={ data.ref } 
                id={ data.ref } 
                style={ this.props.checkboxStyle } 
                hoverStyle={ this.props.hoverCheckboxStyle } 
                checkedStyle={ this.props.checkedCheckboxStyle } 
                textStyle={ this.props.textStyle } 
                hoverTextStyle={ this.props.hoverTextStyle } 
                checkedTextStyle={ this.props.checkedTextStyle } 
                icons={ this.props.icons } 
                checkedIcons={ this.props.checkedIcons } 
                checked={ this.props.checked === data.value } 
                onChange={ this._onChange }
            >{ data.label }</Radio>
        );
    }

    private _onChange = (e: any, checked: boolean, val: string) => {
        let _index = this.state.value.indexOf(val),
            _checkedItems = this.state.value;

        if (checked) {
            _checkedItems = val;

            this.clearValue(_checkedItems);

            this.setState({value: _checkedItems});

            if (typeof this.props.onChange === 'function' && e.event !== 'clear' && e.event !== 'checkAll') 
                this.props.onChange(e, _checkedItems);
        }
    }

    public clearValue = (val?: string) => {
        for (let key in this.refs) {
            if ((this.refs as any)[key].state.checked && val !== (this.refs as any)[key].props.value) (this.refs as any)[key]._clearChecked({event: 'clear'});
        }

        if (typeof this.props.onChange === 'function' && !val) this.props.onChange({event: 'clear'}, '');
    }
}

export = RadioGroup;



