import RX = require('reactxp');
import {Basic as BasicImp} from './Basic';
import {Outline as OutlineImp} from './Outline';
import {Clear as ClearImp} from './Clear';

module ButtonRX {
    export var Basic: typeof BasicImp = BasicImp;
    export var Outline: typeof OutlineImp = OutlineImp;
    export var Clear: typeof BasicImp = ClearImp;
    export interface Type extends BasicImp {};
};

export = ButtonRX;



